.fullscreen-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
}

.fullscreen-container img,
.fullscreen-container video {
    max-width: 90vw;
    max-height: 90vh;
    object-fit: contain;
}
.fullscreen-container video {
	border: none;
}

/* Close button */
.fullscreen-close {
    position: absolute;
    top: 15px;
    right: 20px;
    font-size: 35px;
    color: white;
    cursor: pointer;
}

/* Navigation buttons */
.fullscreen-prev, .fullscreen-next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 25px;
    color: white;
    background: rgba(0, 0, 0, 0.5);
    padding: 13px 15px;
    cursor: pointer;
    border-radius: 50%;
    user-select: none;
}

.fullscreen-prev { left: 20px; }
.fullscreen-next { right: 20px; }

